.nice-alert {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 40vmin;
  min-height: 200px;
  width: 60vmin;
  min-width: 300px;
  border-radius: 50%;
  overflow: hidden;
  outline: none;
  -webkit-transform: rotate(-360deg) scale(0.4, 0.6);
          transform: rotate(-360deg) scale(0.4, 0.6);
  background: white;
  -webkit-animation: nice-alert-start 1s ease forwards, nice-alert-normalize 2.5s 2.2s ease forwards;
          animation: nice-alert-start 1s ease forwards, nice-alert-normalize 2.5s 2.2s ease forwards;
  font-size: calc(10px + 2vmin); }
  .nice-alert::after, .nice-alert::before {
    display: block;
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-transform: scale(0);
            transform: scale(0); }
  .nice-alert::before {
    z-index: 1;
    background-color: #ffc799;
    -webkit-animation: nice-alert-layer 6s 1s ease forwards;
            animation: nice-alert-layer 6s 1s ease forwards; }
  .nice-alert::after {
    z-index: 2;
    background-color: #ff7300;
    /*delay 1.3s*/
    -webkit-animation: nice-alert-layer 6s 1.3s ease forwards;
            animation: nice-alert-layer 6s 1.3s ease forwards; }
  .nice-alert .nice-alert-inner {
    display: grid;
    grid-template-rows: 80% 20%;
    width: 100%;
    height: 100%;
    opacity: 0;
    outline: none;
    -webkit-animation: nice-alert-inner-show 0.3s 3.3s ease forwards;
            animation: nice-alert-inner-show 0.3s 3.3s ease forwards; }
    .nice-alert .nice-alert-inner .nice-alert-content {
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      outline: none; }
    .nice-alert .nice-alert-inner .nice-alert-button {
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      text-align: center;
      color: #ff7300;
      outline: none;
      -webkit-transition: .3s;
      transition: .3s;
      cursor: pointer; }
      .nice-alert .nice-alert-inner .nice-alert-button:hover {
        background: #ffc799; }
  .nice-alert.closing {
    -webkit-animation: nice-alert-start 1s ease forwards, nice-alert-normalize 2.5s 2.2s ease forwards, nice-alert-closing 1s ease forwards;
            animation: nice-alert-start 1s ease forwards, nice-alert-normalize 2.5s 2.2s ease forwards, nice-alert-closing 1s ease forwards; }

